import React from 'react';
import { object, func } from 'prop-types';
import { connect } from 'react-redux';
import { Section } from 'shared/components/layout';

import SvgIcon from '@material-ui/core/SvgIcon';
import { mdiDeleteCircleOutline } from '@mdi/js';

import { AvatarCard } from '../../../../components/AvatarCard/AvatarCard';

import './GroupUsers.local.scss';
import { EditGroupUsersContainer } from './EditGroupUsers';

import { removeUsersFromGroupThunk } from '../../../store/thunks';

export const GroupUsers = ({ group, removeUsersFromGroup }) => {
  return (
    <Section>
      <Section.Header>
        <Section.Title>Users in this group</Section.Title>
      </Section.Header>
      <div>
        <ul className="card-list">
          {group.users.map(user => (
            <li className="card-item" key={user}>
              <AvatarCard
                label={user}
                avatar={{ initial: user.charAt(0) }}
                action={
                  <SvgIcon
                    onClick={e => {
                      e.preventDefault();
                      e.stopPropagation();

                      removeUsersFromGroup(group.name, user);
                    }}
                  >
                    <path d={mdiDeleteCircleOutline} />
                  </SvgIcon>
                }
              />
            </li>
          ))}
          <li className="card-item" key="add-user-to-groups">
            <EditGroupUsersContainer />
          </li>
        </ul>
      </div>
    </Section>
  );
};

GroupUsers.propTypes = {
  group: object,
  removeUsersFromGroup: func
};

export const GroupUsersContainer = connect(
  state => ({
    group: state.groups.selected
  }),
  dispatch => ({
    removeUsersFromGroup: (group, user) => dispatch(removeUsersFromGroupThunk({ group, user }))
  })
)(GroupUsers);
