import React from 'react';
import { useHistory } from 'react-router-dom';
import { object, func, bool } from 'prop-types';
import { connect } from 'react-redux';

import { Paper, Section } from 'shared/components/layout';
import { Button } from 'shared/components/controls';

import { PrivilegesContainer } from './containers/Privileges';
import { UserGroupsContainer } from './containers/UserGroups';

import './UserView.local.scss';
import { removeUserThunk } from '../../../store/thunks';
import { EditUserDetailsContainer } from './containers/EditUserDetails';
import { EditUserInfoContainer } from './containers/EditUserInfo';

export const UserView = ({ user, removeUser, removing }) => {
  const history = useHistory();

  /* This needs to be written as a result check or 404 page for a user. */
  if (!user) {
    return <></>;
  }

  return (
    <Paper flex="column">
      <Paper.Header>
        <Paper.Title>{user.name}</Paper.Title>
      </Paper.Header>
      <Paper.Content>
        <Section>
          <Section.Title>Global Privileges</Section.Title>
          <PrivilegesContainer />
        </Section>
        <Section>
          <Section.Title>Member of Groups</Section.Title>
          <UserGroupsContainer />
        </Section>
      </Paper.Content>
      <Paper.Footer>
        <Paper.Footer.Left>
          <Button
            color="secondary"
            disabled={removing}
            onClick={() => {
              removeUser(user.name)
                .then(() => {
                  history.push('/system/mas-accounts/users');
                })
                .catch(() => {
                  alert('Could not remove user!'); // eslint-disable-line
                });
            }}
          >
            Remove
          </Button>
          <EditUserDetailsContainer />
          <EditUserInfoContainer />
        </Paper.Footer.Left>
      </Paper.Footer>
    </Paper>
  );
};

UserView.propTypes = {
  user: object,
  removeUser: func,
  removing: bool
};

export const ConnectedUserView = connect(
  state => ({
    user: state.usersManager.users.selected,
    removing: state.usersManager.flags.user.removing
  }),
  dispatch => ({
    removeUser: userName => dispatch(removeUserThunk(userName))
  })
)(UserView);
