import { put, takeEvery } from '@redux-saga/core/effects';

import { actions } from 'shared/store/actions';
import { RestClient } from 'shared/lib/rest';
import { CONSTANTS } from 'config';

// import mock from './_mock.json';

function* jobs(query) {
  try {
    const response = yield RestClient.get(CONSTANTS.BASE.ENDPOINTS.JOBS, query.payload);

    // yield put(actions.getJobs.dispatchSuccess(mock));
    yield put(actions.getJobs.dispatchSuccess(response.data));
  } catch (errors) {
    yield put(actions.getJobs.dispatchFail(errors));
  }
}

export function* JobsEffect() {
  yield takeEvery(actions.getJobs.getLoadingAction(), jobs);
}
